@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap");

* {
  margin: 0;
  font-family: Rubik;
  font-style: normal;
  /* background: var(--White, #FFF); */
}

.logo {
  display: grid;
  background: url("./assets/techsol-logo.png");
  width: 100%;
  height: 48px;
  background-repeat: no-repeat;
}

nav {
  display: flex;
  padding: 0.3vw 7vw;
  justify-content: space-between;

  /* border: 2px solid red; */

  background: rgba(255, 255, 255, 0.6);
  box-shadow: 0px 6px 36px 4px rgba(0, 0, 0, 0.06);
  backdrop-filter: blur(25px);

  white-space: nowrap;
}

nav .general-options {
  display: flex;
  align-items: center;
  gap: 24px;
}

nav .story {
  color: var(--Black-500, #667176);
  text-align: center;
  text-decoration: none;
  font-size: 14px;
  font-weight: 400px;
  line-height: 24px;
}

nav .contact {
  color: var(--Black-500, #667176);
  text-align: center;
  text-decoration: none;
  font-size: 14px;
  font-weight: 400px;
  line-height: 24px;
}

nav .general-options>a.active {
  font-weight: 500px;
  color: var(--Blue-400, #009ad6);
}

/* language options */
nav .language-options {
  display: flex;
  padding: 4px;
  align-items: flex-start;
  border-radius: 9px;
  background: var(--Blue-25, #e6f8ff);
}

nav .language-link {
  display: grid;
  padding: 6px 14px;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
  border-radius: 6px;
  text-decoration: none;
}

nav .lang {
  text-align: center;
  font-size: 14px;
  font-weight: 400px;
  line-height: 20px;
}

nav .language-options>a.active {
  background: var(--Blue-400, #009ad6);
  color: var(--White, #fff);
}

nav .language-options>a:active {
  color: var(--Black-500, #667176);
}

/* =========footer========= */
.root-footer {
  display: flex;
  padding: 3vw;
  padding-left: 8vw;
  padding-right: 8vw;
  justify-content: space-between;
  /* border: 1px solid green; */
}

/* grid-1 */

.grid-1 {
  margin-bottom: 2vw;
}

.copy-rights {
  font-size: 12px;
  font-weight: 400px;
  line-height: 20px;
  color: Black 400;
}

.social-medial-links>i {
  margin-right: 1vw;
  color: #009ad6;
}

.footer-logo {
  background: url("./assets/techsol-logo.png");
  width: 120%;
  height: 50px;
  background-repeat: no-repeat;
}

/* grid-2 */

.grid-2>h2 {
  color: var(--Black-600, #334249);
  font-weight: 500px;
  line-height: 32px;
  letter-spacing: -0.48px;
  font-size: 26px;
}

.link {
  margin-bottom: 1vw;
}

#top-grid-2 {
  display: flex;
  flex-direction: column;
  gap: 1vw;
}

.links {
  text-decoration: none;
  color: var(--Black-500, #667176);
  font-size: 16px;
  font-weight: 600;
  line-height: 28px;
}

/* grid-3 */

#top-grid-3 {
  display: flex;
  flex-direction: column;
  margin-bottom: 0.4vw;
}

.grid-3 {
  margin-bottom: 1.5vw;
  width: 25vw;
}

.contacts-grid {
  display: flex;
  margin-bottom: 2vw;
  text-align: left;
}

.contacts-grid:last-child {
  margin-bottom: -2vw;
}

.contacts-grid>i {
  color: #009ad6;
  margin-right: 1vw;
  margin-top: 0.7vw;
}


