/* =============== */
.customer-details {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 4px;
  align-self: stretch;
}

.details-label {
  color: var(--Blue-700, #00222F);
  font-feature-settings: 'clig' off, 'liga' off;
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: 16px;
}

.details-input {
  display: flex;
  padding: 10px 16px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  align-self: stretch;

  border-radius: 4px;
  border: 1px solid var(--Black-300, #D9DCDD);
  background: var(--White, #FFF);

  font-feature-settings: 'clig' off, 'liga' off;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
/* input[type=number] {
  -moz-appearance: textfield;
} */



#get-in-touch {
  color: var(--Black-700, #00131B);
  font-size: 36px;
  font-weight: 800;
  line-height: 52px;
  letter-spacing: -0.40px;
}

#get-in-touch-des {
  align-self: stretch;

  color: var(--Black-500, #667176);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 28px;
}

.customer-form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 24px;
  align-self: stretch;
}

.details-inputTextArea {
  display: flex;
  padding: 10px 16px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  align-self: stretch;

  border-radius: 4px;
  border: 1px solid var(--Black-300, #D9DCDD);
  background: var(--White, #FFF);
}

#submit-button {
  display: flex;
  padding: 14px 32px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  align-self: stretch;
  border-radius: 50px;
  background: var(--Blue-400, #009AD6);
  border: 0;

  color: var(--White, #FFF);
  text-align: center;

  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 28px;

  transition: all 400ms;
  top: 0;
}

#submit-button:hover {
  cursor: pointer;
}

